import {
  Component,
  Input,
  Output,
  EventEmitter,
  OnDestroy,
  AfterViewInit,
  ViewChild,
  ElementRef,
  ViewEncapsulation,
  NgZone,
  ChangeDetectorRef
} from '@angular/core';
import {trigger, animate, transition, style, keyframes, AnimationEvent} from '@angular/animations';
import {isFunction} from 'lodash-es';

import {ANIMATION_TIMING_FUNCTION, ANIMATION_DURATION} from '../common/animation';
import {SafeCdr} from '../common/safe-cdr';
import {AdaptMessage, AdaptToastCloseEvent} from './toast.model';
import {ToastPlacement} from '../common/positioning';

@Component({
  selector: 'adapt-toast-item',
  templateUrl: './toast-item.component.html',
  animations: [
    trigger('toastAnim', [
      transition('void => *', [
        style({transform: 'translate3d(20px, 0px, 0px)', opacity: '0'}),
        animate(ANIMATION_DURATION.medium + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionIn,
          style({transform: 'translate3d(0px, 0px, 0px)', opacity: '1'})
        )
      ]),
      transition('* => void', [
        animate(ANIMATION_DURATION.medium + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionOut,
          keyframes([
            style({
              transform: 'scale(1)',
              opacity: '1',
              paddingBottom: '*',
              paddingTop: '*',
              marginBottom: '*',
              height: '*',
              offset: 0
            }),
            style({
              transform: 'scale(.8)',
              opacity: '0',
              paddingBottom: '*',
              paddingTop: '*',
              marginBottom: '*',
              height: '*',
              offset: .7
            }),
            style({paddingBottom: '0px', paddingTop: '0px', marginBottom: '0px', height: '0px', offset: 1})
          ]))
      ])
    ]),
    trigger('toastAnimLeft', [
      transition('void => *', [
        style({transform: 'translate3d(-20px, 0, 0)', opacity: '0'}),
        animate(ANIMATION_DURATION.medium + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionIn,
          style({transform: 'translate3d(0, 0, 0)', opacity: '1'})
        )
      ]),
      transition('* => void', [
        animate(ANIMATION_DURATION.medium + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionOut,
          keyframes([
            style({
              transform: 'scale(1)', opacity: '1', paddingBottom: '*',
              paddingTop: '*', marginBottom: '*', height: '*', offset: 0
            }),
            style({
              transform: 'scale(.8)', opacity: '0',
              paddingBottom: '*', paddingTop: '*',
              marginBottom: '*', height: '*', offset: .7
            }),
            style({paddingBottom: '0px', paddingTop: '0px', marginBottom: '0px', height: '0px', offset: 1})
          ]))
      ])
    ]),
    trigger('toastAnimTopCenter', [
      transition('void => *', [
        style({transform: 'translate3d(0, -20px, 0)', opacity: '0'}),
        animate(ANIMATION_DURATION.medium + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionIn,
          style({transform: 'translate3d(0, 0, 0)', opacity: '1'})
        )
      ]),
      transition('* => void', [
        animate(ANIMATION_DURATION.medium + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionOut,
          keyframes([
            style({
              transform: 'scale(1)',
              opacity: '1',
              paddingBottom: '*',
              paddingTop: '*',
              marginBottom: '*',
              height: '*',
              offset: 0
            }),
            style({
              transform: 'scale(.8)',
              opacity: '0',
              paddingBottom: '*',
              paddingTop: '*',
              marginBottom: '*',
              height: '*',
              offset: .7
            }),
            style({paddingBottom: '0px', paddingTop: '0px', marginBottom: '0px', height: '0px', offset: 1})
          ]))
      ])
    ])
  ],
  encapsulation: ViewEncapsulation.None
})

export class AdaptToastItemComponent implements OnDestroy, AfterViewInit {
  expandedTextHeight: number;
  nonExpandedTextHeight: number;
  totalHeight: number;

  expanded: boolean = false;

  animationDuration: string = ANIMATION_DURATION.medium;
  timeout: number;

  @Input() message: AdaptMessage; // changed from Message since 10.14.0

  @Input()
  get placement(): ToastPlacement {
    return this._placement;
  }

  set placement(value: ToastPlacement) {
    this._placement = value;
  }

  @Input() index: number;
  @Input() link: string;
  @Input() ariaLabel: string;
  @Input() zIndex: number;

  /* eslint-disable-next-line @angular-eslint/no-output-on-prefix */
  @Output() readonly onClose: EventEmitter<AdaptToastCloseEvent> = new EventEmitter();
  /* eslint-disable-next-line @angular-eslint/no-output-on-prefix */
  @Output() readonly onAnimationEnd: EventEmitter<AdaptMessage> = new EventEmitter<AdaptMessage>(); // changed from 'EventEmitter<any>' since 10.14.0
  @Output() readonly linkClick: EventEmitter<AdaptMessage> = new EventEmitter<AdaptMessage>(); // changed from 'EventEmitter<any>' since 10.14.0

  @ViewChild('nonExpandedDetails', {static: false}) nonExpandedDetails: ElementRef<HTMLSpanElement>;
  @ViewChild('expandedDetails', {static: false}) expandedDetails: ElementRef<HTMLSpanElement>;

  private _placement: ToastPlacement = 'top-right';

  constructor(private _ngZone: NgZone, private _cdr: ChangeDetectorRef) {
  }

  ngAfterViewInit(): void {
    this.initTimeout();
  }

  initTimeout(): void {
    if (!this.message.sticky) {
      this._ngZone.runOutsideAngular(() => {
        this.timeout = window.setTimeout(() => {
          this.onClose.emit({
            index: this.index,
            message: this.message
          });

          SafeCdr.detectChanges(this._cdr);
        }, this.message.life || 3000);
      });
    }
  }

  clearTimeout(): void {
    if (this.timeout) {
      clearTimeout(this.timeout);
      this.timeout = null;
    }
  }

  onMouseEnter(): void {
    this.clearTimeout();
  }

  onMouseLeave(): void {
    this.initTimeout();
  }

  onCloseIconClick(event: Event): void {
    this.clearTimeout();

    this.onClose.emit({
      index: this.index,
      message: this.message
    });

    event.preventDefault();
  }

  /**
   * Show full toast message
   */
  showFullMessage(): void {
    this.expanded = true;

    this.totalHeight = this.nonExpandedTextHeight;

    setTimeout(() => {
      if (this.expandedDetails) {
        this.expandedTextHeight = this.expandedDetails.nativeElement.getBoundingClientRect().height;
        this.totalHeight = this.expandedTextHeight;
      }
    });
  }

  /**
   * Hide full toast message
   */
  hideFullMessage(): void {
    this.expanded = false;
  }

  /**
   * Emit empty value to handle link click
   */
  handleLinkClick(): void {
    this.linkClick.next(this.message);

    if (isFunction(this.message.linkHandler)) {
      this.message.linkHandler(this.message);
    }
  }

  animationDone(event: AnimationEvent): void {
    if (event.toState === 'void') {
      this.onAnimationEnd.next(this.message);
    }

    setTimeout(() => {
      if (this.nonExpandedDetails) {
        this.nonExpandedTextHeight = this.nonExpandedDetails.nativeElement.getBoundingClientRect().height;
      }
    });
  }

  ngOnDestroy(): void {
    this.clearTimeout();
  }
}
